<template>
	<view class="add">
		<form @submit="submitForm">
			<view class="title">
				<input type="text" name="title" placeholder="请输入标题">
			</view>
			<view class="name">
				<input type="text" name="name" placeholder="请输入作者">
			</view>
			<view class="text">
				<textarea name="text" placeholder="请输入内容"> </textarea>
			</view>
			<button form-type="reset">重置</button>
			<button form-type="submit" type="primary">提交</button>
		</form>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				article: {}
			};
		},
		onLoad(e) {

		},
		methods: {
			submitForm(e) {
				if (!e.detail.value.title) {
					uni.showToast({
						title: "请输入标题"
					})
					return
				}
				if (!e.detail.value.name) {
					uni.showToast({
						title: "请输入来源"
					})
					return
				}
				if (!e.detail.value.text) {
					uni.showToast({
						title: "请输入内容"
					})
					return
				}
				uniCloud.callFunction({
					name: "addData",
					data: e.detail.value
				}).then(res => {
					console.log(res);
				})
				uni.navigateTo({
					url: "/pages/index/index"
				})
			}
		}
	}
</script>

<style lang="scss">
	.add {
		view {
			padding: 20rpx;
			border: 1px solid #ddd;
			box-sizing: border-box;
			margin-bottom: 10rpx;
		}
	}
</style>